    AddserviceinfoPage.prototype.getNextDate = function (val, field) {
        console.log('1' + val);
        var date;
        if (val > 0) {
            date = this.addDays(val);
        }
        else {
            this.showDatePicker();
        }
        if (field == '1') {
            this.serviced_datetime = date.getFullYear() + "-" + parseInt(date.getMonth() + 1) + "-" + date.getDate();
        }
        else {
            this.unitDetailData.nextServiceDate = date.getFullYear() + "-" + parseInt(date.getMonth() + 1) + "-" + date.getDate();
        }
    };
    AddserviceinfoPage.prototype.getPrority = function (val) {
        this.priority_highclass = '';
        this.priority_lowclass = '';
        if (val == "2") {
            this.priority_highclass = "border_high";
        }
        if (val == "1") {
            this.priority_lowclass = "border_low";
        }
        this.service_priority = val;
    };
    AddserviceinfoPage.prototype.addDays = function (days) {
        var result = new Date();
        result.setDate(result.getDate() + days);
        return result;
    };
    AddserviceinfoPage.prototype.subDays = function (days) {
        var result = new Date();
        result.setDate(result.getDate() - days);
        console.log("SubDays Function Result is:" + result);
        return result;
    };
    AddserviceinfoPage.prototype.showDatePicker = function () {
        var _this = this;
        console.log("showDatePicker:" + this.servicemaxdate);
        this.datePicker.show({
            date: new Date(),
            mode: 'date',
            maxDate: new Date(),
            allowOldDates: false,
            doneButtonColor: '#F2F3F4',
            cancelButtonColor: '#000000',
            allowFutureDates: true,
            androidTheme: this.datePicker.ANDROID_THEMES.THEME_HOLO_LIGHT
        }).then(function (date) {
            var monthstr = date.getMonth() + parseInt("1");
            console.log('Got date: ', date);
            console.log(new Date().getTime() + "<" + date.getTime());
            if (new Date().getTime() < date.getTime()) {
                _this.isFuture = 0;
                _this.isSubmitted = false;
                console.log("Future Dates");
                _this.unitDetailData.nextServiceDate = date.getFullYear() + "-" + monthstr + "-" + date.getDate();
            }
            else {
                _this.isFuture = 1;
                _this.isSubmitted = true;
                console.log("Old Dates");
                _this.unitDetailData.nextServiceDate = '';
            }
        }, function (err) { return console.log('Error occurred while getting date: ', err); });
    };
